.hexagon {
    background-color: #04c2c9;
    display: inline-block;
    height: 100%;
    width: calc(100% * 0.57735);
  }
  
  .hexagon:before {
    background-color: inherit;
    content: "";
    height: inherit;
    position: absolute;
    right: calc((100% / 2) - ((100% * 0.57735) / 2));
    top: 0;
    transform: rotateZ(60deg);
    width: inherit;
  }
  
  .hexagon:after {
    background-color: inherit;
    content: "";
    height: inherit;
    position: absolute;
    right: calc((100% / 2) - ((100% * 0.57735) / 2));
    top: 0;
    transform: rotateZ(-60deg);
    width: inherit;
  }

  #about .label-wrap .label {
    margin: 20px 0 10px 0;
  }
   #about .bullet-wrap {
    height: 200px;
    /* justify-content: flex-start; */
    /* padding: 0 7px; */
  }
  #about .highlight {
    color: #009ada;
  }
  #about .up-arrow {
    height: 30px;
    margin: 15px 0;
  }
  #about .skills-wrapper {
    align-items: flex-start;
    width: 100%;
  }
  #about .skills-wrapper .me {
    height: 200px;
    border-radius: 50%;
  }
  #about .skills-wrapper .label {
    margin: 20px 0 15px 0;
  }
  #about .hexagon i {
    color: #fff;
    font-size: 34pt;
    left: 0;
    position: absolute;
    right: 0;
    top: 20px;
    z-index: 1;
  }
  #about .bars-wrap { /*esto controla posicion las barras*/
    margin: 50px auto 0 auto;
    max-width: 90%;
    width: calc(100% - 15px);
  }

  #about .bar { /* visualizacion de las barras*/
    align-items: flex-end;
    background-color: #eee;
    color: #666;
    font-size: 7pt;
    height: 22px;
    margin: 0 0 12px 0;
    position: relative;
  }

  #about .bar span {
    display: none;
  }
  #about .bar.fill {
    left: 0;
    position: absolute;
    top: 0;
    background-color: #00a1a7;
    opacity: 1;
    padding: 0;
  }
  #about .bar .tag {
    left: 0;
    position: absolute;
    top: 0;
    background-color: #04c2c9;
    color: #fff;
    height: 100%;
    width: 110px;
    width: 70px;
  }
  #about .bar.secondary {
    background-color: #c51675;
  }
  #about .bar.secondary .tag {
    background-color: #e31b6d;
  }
  #about .bar span {
    padding: 0 15px 0 0;
    z-index: 1;
  }
  #about .flex-50-gt-sm {
    display: block;
  }

  @media screen and (min-width: 600px) {

    #about .label-wrap .label {
        margin: 15px 0 5px 0;
      }

      #about .bullet-wrap {
        height: 150px;
        width: 56px;
        margin: 0 45px 0 45px;

      }
      #about .skills-wrapper > .flex:first-child {
        padding: 15px 50px 0 50px;
      }
      #about .skills-wrapper .me {
        height: 250px;
      }
      #about .bar {
        font-size: 11pt;
        height: 28px;
        margin: 0 0 15px 0;
      }
      #about .bar span {
        display: initial;
      }
      #about .bar .tag {
        width: 110px;
      }
      #about .hexagon i {
        font-size: 36pt;
      }
      .hex-wrap { /*tamaño del hexagono*/
        height: 85px;
        width: 85px;
      }
      .flex.row-gt-sm {
        flex-direction: row; /*Esto pone en posicion de filas los iconos de about*/
      }            
  }  

  @media screen and (min-width: 960px) {
  #about .bars-wrap {
    margin: 0 0 0 0;
  }
  #about .skills-wrapper {
    margin: 15px 0 0 0;
  }
  #about .skills-wrapper .me {
    margin: 0;
  }
  #about .bar {
    max-width: 90%;
  }
  #about .bullet-wrap {
    height: initial;
    width: 56px;
    margin: 0 60px 0 60px;
  }
  #about .hexagon i {
    font-size: 40pt;
  }

  .flex-50-gt-sm {
    /* box-sizing: border-box;
    -webkit-flex: 1 1 50%;
    -ms-flex: 1 1 50%;
    flex: 1 1 50%;
    max-height: 100%;
    max-width: 50%; */
    display: inline-block;

  }

  .hex-wrap { /*tamaño del hexagono*/
    height: 95px;
    width: 95px;
  } 
}